import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { StaticContext } from 'react-router';
import { RouteComponentProps, Route, Redirect } from 'react-router-dom';
import { GET } from '../utils/requests';
import { SingleGame } from '../chess/ChessCom';
import styles from './css/game.module.css';
import chessStyles from '../css/chesscom.module.css';

export default function Game(props: RouteComponentProps<any, StaticContext, any>) {

	const id = props.match.params.id as string;
	const [result, setResult] = useState({} as RawResult);
	const updateResult = useCallback(() => GET({
		url: ['result', id].join('/')
	}).then(setResult), [id, setResult]);
	useEffect(() => {
		updateResult();
	}, [updateResult]);

	const games = useMemo(() => Object.values(result.games || {}), [result.games]);

	const [shouldRedirect, redirectTo] = useState(0);
	if (shouldRedirect && shouldRedirect.toString() !== props.match.params.index) return <Redirect to={'/g/' + id + '/' + shouldRedirect.toString()} />

	return (
		<div id='main' className={'animated-' + props.location.state.direction}>
			<div className={[styles.stage, chessStyles.wrapper].join(' ')}>
				<div className={[styles.viewer, chessStyles.gameViewWrapper].join(' ')}>
					<Route path='/g/:id/:index' render={() => !games[props.match.params.index - 1] ? null : <SingleGame g={games[props.match.params.index - 1]} isComplete />} />
				</div>
				<div className={[styles.more].join(' ')}>
					{games.map((r, i) => <div onClick={() => redirectTo(i + 1)}><SingleGame g={r} isComplete /></div>)}
				</div>
			</div>
		</div>
	)

}

interface RawResult {
    input:            Input;
    fetched:          Fetched;
    games:            Game[];
    id:               string;
    idW:              string;
    idB:              string;
    tournamentId:     string;
    round:            number;
    resultWhite:      number;
    resultBlack:      number;
    matchW:           number;
    matchB:           number;
    ratingW:          number;
    ratingB:          number;
    expectedW:        number;
    expectedB:        number;
    chessComTeamLink: string;
    lichessTeamLink:  null;
    chessComLink:     null;
    lichessLink:      null;
    createdAt:        Date;
    updatedAt:        Date;
}

interface Input {
    scores: number[];
    user:   string;
    at:     number;
}

interface Fetched {
    startTime:  number;
    endTime:    number;
    scores:     number[];
    isFinished: boolean;
}

interface Game {
    board: number;
    game:  number;
    pgn:   string;
}